.backdrop {
  position: fixed;
  inset: 0;

  background: rgba(0, 0, 0, 0.3);

  visibility: visible;
  opacity: 1;

  transition: opacity 0.3s ease-in-out;

  &.is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }

  &.is-hidden .modal {
    transform: translate(-50%, -50%) scale(0.9);
  }
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;

  //   width: 100%;
  //   max-width: 700px;
  //   max-height: 500px;

  padding: 40px 60px;

  background: var(--hover-background-color);
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.12), 1px 3px 3px rgba(0, 0, 0, 0.14),
    1px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;

  transform: translate(-50%, -50%) scale(1);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__close--icon {
  width: 30px;
  height: 30px;

  border-radius: 50%;

  stroke: var(--paragraph-color);
  cursor: pointer;

  transition: all 0.3s ease-in-out;

  &:hover {
    background: var(--hover-background-color);
  }
}
